/* 히어로 섹션 스타일 */
.heroSection {
    background-color: #59BAFF;
    padding-top: 9rem;
    padding-bottom: 2rem;
    padding-left: 2rem;
    padding-right: 2rem;    
    text-align: center;
    color: white;
}

.heroTitle {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.searchContainer {
    max-width: 800px;
    margin: 0 auto;
    position: relative;
}

.searchInput {
    width: 100%;
    padding: 1rem;
    border-radius: 25px;
    border: none;
    font-size: 24px;
}

.searchButton {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
}

.searchTags {
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    gap: 2rem;
}

.tag {
    background-color: white;
    border: none;
    padding: 0.5rem 2rem;
    width: fit-content;
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    white-space: nowrap;
}

.features a {
    color: inherit; /* 링크의 기본 색상 상속 */
    text-decoration: none; /* 링크의 기본 밑줄 제거 */
}

.features a:hover {
    color: inherit; /* 호버 시 색상 유지 */
    text-decoration: none; /* 호버 시 밑줄 제거 */
}

/* 특징 섹션 스타일 */
.features {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    padding: 20px;
    max-width: 1100px;
    margin: 0 auto;
}

.featureItem {
    display: flex;
    align-items: center;
    background: white;
    padding: 10px;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /* 그림자 효과 추가 */
    height: 100px;
    width: 100%;
    transition: box-shadow 0.2s ease; /* 부드러운 그림자 전환 효과 */
}

.featureImage {
    width: 60px;
    height: 60px;
    object-fit: contain;
    margin-right: 16px;
}

.featureText {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.featureText h3 {
    margin: 0;
    font-weight: 600;
    font-size: 16px;
    text-align: center;
}

.featureText span {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    text-align: center;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
    .features {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px) {
    .features {
        grid-template-columns: 1fr;
    }
}

.tooltipContainer {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin-left: 4px;
}

.tooltip {
    visibility: hidden;
    position: absolute;
    background-color: black;
    color: white !important;
    text-align: center;
    padding: 8px 12px;
    border-radius: 8px;
    font-size: 13px;
    font-weight: normal;
    
    /* 툴팁 위치 조정 */
    bottom: 125%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 5px;
    
    /* 툴팁이 나타날 때의 애니메이션 */
    opacity: 0;
    transition: opacity 0.2s;
    
    white-space: nowrap;
    z-index: 999;
}

/* 툴팁 화살표 */
.tooltip::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
}

/* 호버 시 툴팁 표시 */
.tooltipContainer:hover .tooltip {
    visibility: visible;
    opacity: 1;
}

/* 정보 아이콘 스타일링 */
.tooltipContainer img {
    cursor: pointer;
    vertical-align: middle;
}